import React, { useState } from 'react'
import SuperButton from '../hw04/common/c2-SuperButton/SuperButton'
import { restoreState } from '../hw06/localStorage/localStorage'
import s from './Clock.module.css'

function Clock() {
    const [timerId, setTimerId] = useState<number | undefined>(undefined)
    // for autotests //   //      ,     
    const [date, setDate] = useState<Date>(new Date(restoreState('hw9-date', Date.now())))
    const [show, setShow] = useState<boolean>(false)

    const start = () => {
        //   //   (   ,   +1)
        //    (https://learn.javascript.ru/settimeout-setinterval#setinterval)
        const timerId = setInterval(() => {
            setDate(new Date(Date.now()))
        }, 1000)
        setTimerId(Number(timerId))

    }

    const stop = () => {
        //   //    ,    (timerId <- undefined)
        if (timerId !== undefined) {
            clearInterval(timerId)
            setTimerId(undefined)
        }
    }

    const onMouseEnter = () => { //   //     
        setShow(true)
    }
    const onMouseLeave = () => { //   //      
        setShow(false)
    }

    const formatTime = ( ms: number ) => {
        const date = new Date(ms);
        const hours = String(date.getHours()).padStart(2, '0');
        const minutes = String(date.getMinutes()).padStart(2, '0');
        const seconds = String(date.getSeconds()).padStart(2, '0');
        return `${ hours }:${ minutes }:${ seconds }`;
    }

    function formatDate( date: Date ): string {
        const day = String(date.getDate()).padStart(2, '0');
        const month = String(date.getMonth() + 1).padStart(2, '0');
        const year = date.getFullYear();
        return `${ day }.${ month }.${ year }`;
    }

    const stringTime = formatTime(date.getTime()) || <br /> // 24:: (01:02:03)/(23:02:03)/(24:00:00)/(00:00:01) //  
    const stringDate = formatDate(date) || <br /> // .. (01.02.2022) //  ,  01.02.0123/01.02.-123/01.02.12345  

    //    ,    (https://learn.javascript.ru/intl#intl-datetimeformat)
    const daysOfWeek = ['Sunday', 'Monday', 'Tuesday', 'Wednesday', 'Thursday', 'Friday', 'Saturday'];
    const stringDay = daysOfWeek[date.getDay()] || <br /> //  

    const months = ['January', 'February', 'March', 'April', 'May', 'June', 'July', 'August', 'September', 'October', 'November', 'December']
    const stringMonth = months[date.getMonth()] || <br /> //  

    return (
        <div className={ s.clock }>
            <div
                id={ 'hw9-watch' }
                className={ s.watch }
                onMouseEnter={ onMouseEnter }
                onMouseLeave={ onMouseLeave }
            >
                <span id={ 'hw9-day' }>{ stringDay }</span>,{ ' ' }
                <span id={ 'hw9-time' }>
                    <strong>{ stringTime }</strong>
                </span>
            </div>

            <div id={ 'hw9-more' }>
                <div className={ s.more }>
                    { show ? (
                        <>
                            <span id={ 'hw9-month' }>{ stringMonth }</span>,{ ' ' }
                            <span id={ 'hw9-date' }>{ stringDate }</span>
                        </>
                    ) : (
                        <>
                            <br />
                        </>
                    ) }
                </div>
            </div>

            <div className={ s.buttonsContainer }>
                <SuperButton
                    id={ 'hw9-button-start' }
                    disabled={ timerId !== undefined } //   //    
                    onClick={ start }
                >
                    start
                </SuperButton>
                <SuperButton
                    id={ 'hw9-button-stop' }
                    disabled={ timerId == undefined } //   //     
                    onClick={ stop }
                >
                    stop
                </SuperButton>
            </div>
        </div>
    )
}

export default Clock
